          </div>
          
          <button @click="publishPost" class="publish-btn">ÂèëÂ∏É</button>
        </div>
      </div>
    </div>

    <!-- Á≠õÈÄâÂíåÊêúÁ¥¢ -->
    <div class="filter-bar">
      <div class="filter-options">
        <button
          v-for="filter in filters"
          :key="filter.value"
          :class="['filter-btn', { active: currentFilter === filter.value }]"
