      <div class="search-box">
        <input
          v-model="searchKeyword"
          @input="handleSearch"
          placeholder="ÊêúÁ¥¢Âä®ÊÄÅ..."
          class="search-input"
        />
        <button @click="handleSearch" class="search-btn">ÊêúÁ¥¢</button>
      </div>
    </div>

    <!-- Âä®ÊÄÅÂàóË°® -->
    <div class="post-list">
      <div v-for="post in posts" :key="post.id" class="post-item">
        <div class="post-header">
          <div class="post-user">
            <div class="user-avatar">
              <img :src="userAvatar" alt="Áî®Êà∑Â§¥ÂÉè" />
            </div>
            <div class="user-info">
              <div class="user-name">{{ getUserName(post.userId, post) }}</div>
              <div class="post-time">{{ formatTime(post.createdAt) }}</div>
            </div>
          </div>
          
          <!-- ÈöêÁßÅÊ†áËØÜ -->
          <div class="privacy-indicator" :title="getPrivacyText(post.privacy)">
            {{ post.privacy === 'public' ? 'üåç' : 'üîí' }}
          </div>
        </div>
        
        <!-- Âä®ÊÄÅÂÜÖÂÆπ -->
        <div class="post-content">{{ post.content }}</div>
        
        <!-- Âä®ÊÄÅÂõæÁâá -->
        <div v-if="post.images && post.images.length > 0" class="post-images">
          <img
            v-for="(image, index) in post.images"
            :key="index"
            :src="image.imageUrl"
            alt="Âä®ÊÄÅÂõæÁâá"
            class="post-image"
          />
        </div>
        
        <!-- Âä®ÊÄÅÊ†áÁ≠æ -->
        <div v-if="post.tags && post.tags.length > 0" class="post-tags">
          <span
            v-for="tag in post.tags"
            :key="tag.id"
            class="post-tag"
            @click="filterByTag(tag.id)"
          >
            #{{ tag.name }}
          </span>
        </div>
        
        <!-- ‰∫íÂä®ÁªüËÆ° -->
        <div class="post-stats">
          <span class="stat-item">
            <span class="icon">üëÅÔ∏è</span> {{ post.viewCount }}
          </span>
          <span class="stat-item">
            <span class="icon">‚ù§Ô∏è</span> {{ post.likeCount }}
          </span>
          <span class="stat-item">
            <span class="icon">üí¨</span> {{ post.commentCount }}
          </span>
        </div>
        
        <!-- ‰∫íÂä®ÊåâÈíÆ -->
        <div class="post-actions">
          <button
            :class="['action-btn', { active: isLiked(post.id) }]"
            @click="toggleLike(post.id)"
          >
            <span class="icon">‚ù§Ô∏è</span> {{ isLiked(post.id) ? 'ÂèñÊ∂àÁÇπËµû' : 'ÁÇπËµû' }}
          </button>
          <button @click="showComments(post.id)" class="action-btn">
            <span class="icon">üí¨</span> ËØÑËÆ∫
          </button>
        </div>
        
        <!-- ËØÑËÆ∫Âå∫ -->
        <div v-if="openCommentsPostId === post.id" class="comments-section">
          <div v-if="getPostComments(post.id).length > 0" class="comment-list">
            <div
              v-for="comment in getPostComments(post.id)"
              :key="comment.id"
              class="comment-item"
            >
              <div class="comment-avatar">
                <img :src="userAvatar" alt="Áî®Êà∑Â§¥ÂÉè" />
              </div>
              <div class="comment-content">
                <div class="comment-user">{{ comment.user ? (comment.user.realName || comment.user.username) : getUserName(comment.userId) }}</div>
                <div class="comment-text">{{ comment.content }}</div>
                <div class="comment-time">{{ formatTime(comment.createdAt) }}</div>
              </div>
              
              <!-- ÊòæÁ§∫ÂõûÂ§ç -->
              <div v-if="comment.replies && comment.replies.length > 0" class="comment-replies">
                <div
                  v-for="reply in comment.replies"
                  :key="reply.id"
                  class="reply-item"
                >
                  <div class="comment-avatar">
                    <img :src="userAvatar" alt="Áî®Êà∑Â§¥ÂÉè" />
                  </div>
                  <div class="comment-content">
                    <div class="comment-user">{{ reply.user ? (reply.user.realName || reply.user.username) : getUserName(reply.userId) }}</div>
                    <div class="comment-text">{{ reply.content }}</div>
                    <div class="comment-time">{{ formatTime(reply.createdAt) }}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          
          <!-- ÂèëË°®ËØÑËÆ∫ -->
          <div class="comment-form">
            <input
              v-model="newComment.content"
              :data-post-id="post.id"
              placeholder="ÂÜô‰∏ã‰Ω†ÁöÑËØÑËÆ∫..."
              class="comment-input"
              @keyup.enter="addComment(post.id)"
            />
            <button @click="addComment(post.id)" class="comment-btn">ÂèëÈÄÅ</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Âä†ËΩΩÊõ¥Â§ö -->
    <div class="load-more">
      <button @click="loadMorePosts" v-if="hasMorePosts" class="load-more-btn">
        Âä†ËΩΩÊõ¥Â§ö
      </button>
      <div v-else class="no-more">Ê≤°ÊúâÊõ¥Â§öÂä®ÊÄÅ‰∫Ü</div>
    </div>

    <!-- ÈÄöÁü•ÂºπÁ™ó -->
    <div v-if="showNotificationModal" class="notification-modal">
      <div class="modal-content">
        <div class="modal-header">
          <h3>ÈÄöÁü•</h3>
          <button @click="showNotificationModal = false" class="close-btn">√ó</button>
        </div>
        <div class="modal-body">
          <div v-if="notifications.length === 0" class="no-notifications">
            ÊöÇÊó†ÈÄöÁü•
          </div>
          <div v-else class="notification-list">
            <div
              v-for="notification in notifications"
              :key="notification.id"
              :class="['notification-item', { read: notification.isRead }]"
              @click="markNotificationAsRead(notification.id)"
            >
              <div class="notification-content">{{ notification.content }}</div>
              <div class="notification-time">{{ formatTime(notification.createdAt) }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { apiRequest } from '../services/api.js'

export default {
  name: 'ResidentCommunity',
  data() {
    return {
      // Áî®Êà∑‰ø°ÊÅØ
      userAvatar: 'data:image/svg+xml;charset=UTF-8,%3Csvg xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22 width%3D%2250%22 height%3D%2250%22 viewBox%3D%220 0 50 50%22%3E%3Ccircle cx%3D%2225%22 cy%3D%2225%22 r%3D%2225%22 fill%3D%22%234CAF50%22%2F%3E%3Ctext x%3D%2225%22 y%3D%2235%22 font-family%3D%22Arial%22 font-size%3D%2230%22 text-anchor%3D%22middle%22 fill%3D%22white%22%3Eüë§%3C%2Ftext%3E%3C%2Fsvg%3E',
      userId: parseInt(this.getLocalStorageItem('userId') || '0'),
      
      // Êñ∞Âä®ÊÄÅ
      newPost: {
        content: '',
        privacy: 'public',
        images: [],
        tagIds: [],
        visibleUserIds: []
      },
      selectedTagId: '',
      selectedUserId: '',
      
      // Ê†áÁ≠æ
      tags: [],
      residents: [],
      
      // Âä®ÊÄÅÂàóË°®
      posts: [],
      page: 1,
      pageSize: 10,
      hasMorePosts: true,
      
      // Á≠õÈÄâÂíåÊêúÁ¥¢
      currentFilter: 'all',
      filters: [
        { value: 'all', label: 'ÂÖ®ÈÉ®' },
        { value: 'time', label: 'ÊúÄÊñ∞' },
        { value: 'popularity', label: 'ÁÉ≠Èó®' }
      ],
      searchKeyword: '',
      
      // ËØÑËÆ∫
      openCommentsPostId: null,
      comments: {},
      loadingComments: new Set(),
      newComment: { content: '' },
      
      // ÁÇπËµûÁä∂ÊÄÅ
      likedPosts: new Set(),
      
      // ÈÄöÁü•
      notifications: [],
      unreadNotifications: 0,
      showNotificationModal: false,
      
      // ËΩÆËØ¢ÂÆöÊó∂Âô®
      pollingTimer: null,
      pollingInterval: 10000 // 10ÁßíËΩÆËØ¢‰∏ÄÊ¨°
    }
  },
  
  mounted() {
    this.loadTags()
    this.loadResidents()
    this.loadTimeline()
    this.loadNotifications()
    // ÂêØÂä®ËΩÆËØ¢
    this.startPolling()
  },
  
  beforeUnmount() {
    this.stopPolling()
  },
  
  methods: {
    // ÂÆâÂÖ®Ëé∑ÂèñlocalStorageÈ°πÁöÑËæÖÂä©ÂáΩÊï∞
    getLocalStorageItem(key) {
      if (typeof localStorage !== 'undefined') {
        return localStorage.getItem(key)
      }
      return null
    },
    // ÂêØÂä®ËΩÆËØ¢
    startPolling() {
      if (this.pollingTimer) {
        clearInterval(this.pollingTimer)
      }
      this.pollingTimer = setInterval(() => {
        this.pollForUpdates()
      }, this.pollingInterval)
    },
    
    // ÂÅúÊ≠¢ËΩÆËØ¢
    stopPolling() {
      if (this.pollingTimer) {
        clearInterval(this.pollingTimer)
        this.pollingTimer = null
      }
    },
    
    // ËΩÆËØ¢Ê£ÄÊü•Êõ¥Êñ∞
    async pollForUpdates() {
      // Ê£ÄÊü•Êñ∞ÈÄöÁü•
      await this.checkNewNotifications()
      
      // Ê£ÄÊü•Êñ∞Âä®ÊÄÅÔºà‰ªÖÂú®Êó∂Èó¥Á∫øËßÜÂõæÔºâ
      if (this.currentFilter === 'all' && this.page === 1) {
        await this.checkNewPosts()
      }
    },
    
    // Ê£ÄÊü•Êñ∞ÈÄöÁü•
    async checkNewNotifications() {
      try {
        const response = await apiRequest('/community/notifications?page=1&pageSize=10')
        const newNotifications = response.data
        const oldUnreadCount = this.unreadNotifications
        const newUnreadCount = newNotifications.filter(n => !n.isRead).length
        
        // Êõ¥Êñ∞ÈÄöÁü•ÂàóË°®ÂíåÊú™ËØªÊï∞
        this.notifications = newNotifications
        this.unreadNotifications = newUnreadCount
        
        // Â¶ÇÊûúÊúâÊñ∞ÈÄöÁü•ÔºåÊòæÁ§∫ÊèêÁ§∫
        if (newUnreadCount > oldUnreadCount) {
          console.log(`Êî∂Âà∞ ${newUnreadCount - oldUnreadCount} Êù°Êñ∞ÈÄöÁü•`)
        }
      } catch (error) {
        console.error('Ê£ÄÊü•Êñ∞ÈÄöÁü•Â§±Ë¥•:', error)
      }
    },
    
    // Ê£ÄÊü•Êñ∞Âä®ÊÄÅ
    async checkNewPosts() {
      try {
        const response = await apiRequest('/community/timeline?page=1&pageSize=10')
        const newPosts = response.data
        
        // Ê£ÄÊü•ÊòØÂê¶ÊúâÊñ∞Â∏ñÂ≠ê
        if (newPosts.length > 0 && this.posts.length > 0) {
          const latestPostId = this.posts[0].id
          const newPostsToAdd = []
          
          for (const post of newPosts) {
            if (post.id > latestPostId) {
              newPostsToAdd.push(post)
            }
          }
          
          // Ê∑ªÂä†Êñ∞Â∏ñÂ≠êÂà∞ÂàóË°®È°∂ÈÉ®
          if (newPostsToAdd.length > 0) {
            newPostsToAdd.reverse().forEach(post => {
              this.posts.unshift(post)
              this.checkLikeStatus(post.id)
            })
            console.log(`Êî∂Âà∞ ${newPostsToAdd.length} Êù°Êñ∞Âä®ÊÄÅ`)
          }
        }
      } catch (error) {
        console.error('Ê£ÄÊü•Êñ∞Âä®ÊÄÅÂ§±Ë¥•:', error)
      }
    },
    
    // Âä†ËΩΩÊ†áÁ≠æ
    async loadTags() {
      try {
        const response = await apiRequest('/community/tags')
        this.tags = response.data
      } catch (error) {
        console.error('Âä†ËΩΩÊ†áÁ≠æÂ§±Ë¥•:', error)
      }
    },
    
    // Âä†ËΩΩÂ±ÖÊ∞ëÂàóË°®
    async loadResidents() {
      try {
        const response = await apiRequest('/residents')
        // Á°Æ‰øùresidentsÂßãÁªàÊòØ‰∏Ä‰∏™Êï∞ÁªÑ
        this.residents = Array.isArray(response.data) ? response.data : []
      } catch (error) {
        console.error('Âä†ËΩΩÂ±ÖÊ∞ëÂàóË°®Â§±Ë¥•:', error)
        // ÂèëÁîüÈîôËØØÊó∂Á°Æ‰øùresidentsÊòØ‰∏Ä‰∏™Êï∞ÁªÑ
        this.residents = []
      }
    },
    
    // Âä†ËΩΩÊó∂Èó¥Á∫ø
    async loadTimeline() {
      try {
        let endpoint = `/community/timeline?page=${this.page}&pageSize=${this.pageSize}`
        
        if (this.currentFilter === 'time') {
          endpoint = `/community/filter/time?timeRange=latest&page=${this.page}&pageSize=${this.pageSize}`
        } else if (this.currentFilter === 'popularity') {
          endpoint = `/community/filter/popularity?page=${this.page}&pageSize=${this.pageSize}`
        }
        
        const response = await apiRequest(endpoint)
        
        const newPosts = response.data
        if (newPosts.length < this.pageSize) {
          this.hasMorePosts = false
        }
        
        // Ë∞ÉËØïÊó•ÂøóÔºöÊü•Áúãpost.idÁöÑÂÆûÈôÖÂÄº
        console.log('‰ªéÂêéÁ´ØËøîÂõûÁöÑposts:', newPosts)
        newPosts.forEach(post => {
          console.log('post.id:', post.id, 'Á±ªÂûã:', typeof post.id)
        })
        
        this.posts = [...this.posts, ...newPosts]
        
        // Âä†ËΩΩÁÇπËµûÁä∂ÊÄÅ
        newPosts.forEach(post => {
          this.checkLikeStatus(post.id)
        })
        
        this.page++
      } catch (error) {
        console.error('Âä†ËΩΩÊó∂Èó¥Á∫øÂ§±Ë¥•:', error)
      }
    },
    
    // Âä†ËΩΩÊõ¥Â§ö
    loadMorePosts() {
      this.loadTimeline()
    },
    
    // ÂõæÁâá‰∏ä‰º†
    async handleImageUpload(event) {
      const files = event.target.files
      if (files.length === 0) return
      
      // ÈÅçÂéÜÊâÄÊúâÈÄâÊã©ÁöÑÊñá‰ª∂
      for (let i = 0; i < files.length; i++) {
        const formData = new FormData()
        formData.append('image', files[i])
        
        try {
          // ÂõæÁâá‰∏ä‰º†ÈúÄË¶Å‰ΩøÁî®axiosÁõ¥Êé•Ë∞ÉÁî®ÔºåÂõ†‰∏∫formData‰∏çËÉΩ‰ΩøÁî®apiRequest
          const response = await axios.post('http://localhost:8080/backend/api/community/upload', formData, {
            headers: {
              'Content-Type': 'multipart/form-data',
              Authorization: `Bearer ${this.getLocalStorageItem('token')}`
            }
          })
          
          this.newPost.images.push(response.data.data)
        } catch (error) {
          console.error('‰∏ä‰º†ÂõæÁâáÂ§±Ë¥•:', error)
        }
      }
    },
    
    // ÁßªÈô§ÂõæÁâá
    removeImage(index) {
      this.newPost.images.splice(index, 1)
    },
    
    // Ê∑ªÂä†Ê†áÁ≠æ
    addTag() {
      if (this.selectedTagId && !this.newPost.tagIds.includes(parseInt(this.selectedTagId))) {
        this.newPost.tagIds.push(parseInt(this.selectedTagId))
        this.selectedTagId = ''
      }
    },
    
    // Ê∑ªÂä†ÂèØËßÅÁî®Êà∑
    addVisibleUser() {
      if (this.selectedUserId && !this.newPost.visibleUserIds?.includes(parseInt(this.selectedUserId))) {
        if (!this.newPost.visibleUserIds) {
          this.newPost.visibleUserIds = []
        }
        this.newPost.visibleUserIds.push(parseInt(this.selectedUserId))
        this.selectedUserId = ''
      }
    },
    
    // ÁßªÈô§ÂèØËßÅÁî®Êà∑
    removeVisibleUser(userId) {
      if (this.newPost.visibleUserIds) {
        this.newPost.visibleUserIds = this.newPost.visibleUserIds.filter(id => id !== userId)
      }
    },
    
    // ÂèëÂ∏ÉÂä®ÊÄÅ
    async publishPost() {
      if (!this.newPost.content.trim()) return
      
      try {
        const response = await apiRequest('/community/publish', 'POST', this.newPost)
        
        // Ê∑ªÂä†Âà∞Âä®ÊÄÅÂàóË°®È°∂ÈÉ®
        this.posts.unshift(response.data)
        
        // Ê∏ÖÁ©∫Ë°®Âçï
        this.newPost = {
          content: '',
          privacy: 'public',
          images: [],
          tagIds: [],
          visibleUserIds: []
        }
      } catch (error) {
        console.error('ÂèëÂ∏ÉÂä®ÊÄÅÂ§±Ë¥•:', error)
      }
    },
    
    // ÂàáÊç¢ÁÇπËµû
    async toggleLike(postId) {
      try {
        // Á°Æ‰øùpostIdÊòØ‰∏Ä‰∏™ÊúâÊïàÁöÑÊï∞Â≠ó
        const numericPostId = parseInt(postId)
        if (isNaN(numericPostId)) {
          console.error('Êó†ÊïàÁöÑpostId:', postId)
          return
        }
        await apiRequest(`/community/like/${numericPostId}`, 'POST')
        
        // Êõ¥Êñ∞ÁÇπËµûÁä∂ÊÄÅ
        if (this.likedPosts.has(numericPostId)) {
          this.likedPosts.delete(numericPostId)
          // Êõ¥Êñ∞ÁÇπËµûÊï∞
          const post = this.posts.find(p => p.id === numericPostId)
          if (post) post.likeCount--
        } else {
          this.likedPosts.add(numericPostId)
          // Êõ¥Êñ∞ÁÇπËµûÊï∞
          const post = this.posts.find(p => p.id === numericPostId)
          if (post) post.likeCount++
        }
      } catch (error) {
        console.error('ÁÇπËµûÊìç‰ΩúÂ§±Ë¥•:', error)
      }
    },
    
    // Ê£ÄÊü•ÁÇπËµûÁä∂ÊÄÅ
    async checkLikeStatus(postId) {
      try {
        // Á°Æ‰øùpostIdÊòØ‰∏Ä‰∏™ÊúâÊïàÁöÑÊï∞Â≠ó
        const numericPostId = parseInt(postId)
        if (isNaN(numericPostId)) {
          console.error('Êó†ÊïàÁöÑpostId:', postId)
          return
        }
        const response = await apiRequest(`/community/like/${numericPostId}`)
        
        if (response.data) {
          this.likedPosts.add(numericPostId)
        }
      } catch (error) {
        console.error('Ê£ÄÊü•ÁÇπËµûÁä∂ÊÄÅÂ§±Ë¥•:', error)
      }
    },
    
    // ÊòØÂê¶ÁÇπËµû
    isLiked(postId) {
      return this.likedPosts.has(parseInt(postId))
    },
    
    // ÊòæÁ§∫/ÈöêËóèËØÑËÆ∫
    showComments(postId) {
      if (this.openCommentsPostId === postId) {
        this.openCommentsPostId = null
      } else {
        this.openCommentsPostId = postId
        this.loadComments(postId)
      }
    },
    
    // Âä†ËΩΩËØÑËÆ∫
    async loadComments(postId) {
      // Á°Æ‰øùpostIdÊòØ‰∏Ä‰∏™ÊúâÊïàÁöÑÊï∞Â≠ó
      const numericPostId = parseInt(postId)
      if (isNaN(numericPostId)) {
        console.error('Âä†ËΩΩËØÑËÆ∫Â§±Ë¥•: Êó†ÊïàÁöÑpostId:', postId)
        return
      }
      
      try {
        // ËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
        this.loadingComments.add(numericPostId)
        
        console.log(`Ê≠£Âú®Âä†ËΩΩÂ∏ñÂ≠ê ${numericPostId} ÁöÑËØÑËÆ∫`)
        const response = await apiRequest(`/community/post/comments/${numericPostId}`)
        
        // È™åËØÅÂìçÂ∫îÊï∞ÊçÆÁªìÊûÑ
        if (Array.isArray(response.data)) {
          // ‰ΩøÁî®‰∏ÄËá¥ÁöÑÊï∞Â≠óÁ±ªÂûã‰Ωú‰∏∫ÈîÆ
          this.comments[numericPostId] = response.data
          console.log(`ÊàêÂäüÂä†ËΩΩÂ∏ñÂ≠ê ${numericPostId} ÁöÑËØÑËÆ∫ÔºåÂÖ± ${response.data.length} Êù°`)
        } else {
          console.error('Âä†ËΩΩËØÑËÆ∫Â§±Ë¥•: Êó†ÊïàÁöÑËØÑËÆ∫Êï∞ÊçÆÊ†ºÂºè:', response.data)
        }
      } catch (error) {
        console.error(`Âä†ËΩΩÂ∏ñÂ≠ê ${numericPostId} ÁöÑËØÑËÆ∫Â§±Ë¥•:`, error || 'Êú™Áü•ÈîôËØØ')
        if (error.response) {
          // ÊúçÂä°Âô®ËøîÂõû‰∫ÜÈîôËØØÁä∂ÊÄÅÁ†Å
          console.error('HTTPÁä∂ÊÄÅ:', error.response.status)
          if (error.response.status === 401) {
            console.error('Êú™ÊéàÊùÉËÆøÈóÆÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï')
          } else if (error.response.status === 404) {
            console.error('ËØ∑Ê±ÇÁöÑËØÑËÆ∫‰∏çÂ≠òÂú®')
          } else {
            console.error('ÊúçÂä°Âô®ÈîôËØØ:', error.response.data || 'Êú™Áü•ÈîôËØØ')
          }
        } else if (error.request) {
          // ËØ∑Ê±ÇÂ∑≤ÂèëÈÄÅ‰ΩÜÊ≤°ÊúâÊî∂Âà∞ÂìçÂ∫î
          console.error('ÊúçÂä°Âô®Êó†ÂìçÂ∫îÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•ÊàñÊúçÂä°Âô®Áä∂ÊÄÅ')
        } else {
          // ËØ∑Ê±ÇÈÖçÁΩÆÈîôËØØ
          console.error('ËØ∑Ê±ÇÈîôËØØ:', error.message || 'Êú™Áü•ÈîôËØØ')
        }
      } finally {
        // Ê∏ÖÈô§Âä†ËΩΩÁä∂ÊÄÅ
        this.loadingComments.delete(numericPostId)
        console.log(`Â∏ñÂ≠ê ${numericPostId} ÁöÑËØÑËÆ∫Âä†ËΩΩÂÆåÊàêÊàñÂ§±Ë¥•`)
      }
    },
    
    // Ê∑ªÂä†ËØÑËÆ∫
    async addComment(postId) {
      const content = this.newComment.content.trim()
      if (!content) return
      
      try {
        // Á°Æ‰øùpostIdÊòØ‰∏Ä‰∏™ÊúâÊïàÁöÑÊï∞Â≠ó
        const numericPostId = parseInt(postId)
        if (isNaN(numericPostId)) {
          console.error('Êó†ÊïàÁöÑpostId:', postId)
          console.error('Êó†ÊïàÁöÑÂ∏ñÂ≠êID')
          return
        }
        const response = await apiRequest(`/community/comment/${numericPostId}`, 'POST', { content })
        
        // Ê∑ªÂä†Âà∞ËØÑËÆ∫ÂàóË°®Ôºà‰ΩøÁî®‰∏ÄËá¥ÁöÑÊï∞Â≠óÁ±ªÂûã‰Ωú‰∏∫ÈîÆÔºâ
        if (!this.comments[numericPostId]) {
          this.comments[numericPostId] = []
        }
        this.comments[numericPostId].push(response.data)
        
        // Êõ¥Êñ∞ËØÑËÆ∫Êï∞
        const post = this.posts.find(p => p.id === numericPostId)
        if (post) post.commentCount++
        
        // Ê∏ÖÁ©∫ËæìÂÖ•
        this.newComment.content = ''
      } catch (error) {
        console.error('Ê∑ªÂä†ËØÑËÆ∫Â§±Ë¥•:', error || 'Êú™Áü•ÈîôËØØ')
      }
    },
    
    // Ëé∑ÂèñËØÑËÆ∫
    getPostComments(postId) {
      const numericPostId = parseInt(postId)
      return this.comments[numericPostId] || []
    },
    
    // Á≠õÈÄâ
    setFilter(filter) {
      this.currentFilter = filter
      this.page = 1
      this.posts = []
      this.hasMorePosts = true
      this.loadTimeline()
    },
    
    // ÊêúÁ¥¢
    handleSearch() {
      if (!this.searchKeyword.trim()) {
        this.page = 1
        this.posts = []
        this.hasMorePosts = true
        this.loadTimeline()
        return
      }
      
      this.searchPosts()
    },
    
    // ÊêúÁ¥¢Âä®ÊÄÅ
    async searchPosts() {
      try {
        const response = await apiRequest(`/community/search?keyword=${this.searchKeyword}&page=1&pageSize=${this.pageSize}`)
        
        this.posts = response.data
        this.hasMorePosts = response.data.length === this.pageSize
      } catch (error) {
        console.error('ÊêúÁ¥¢Âä®ÊÄÅÂ§±Ë¥•:', error)
      }
    },
    
    // ÊåâÊ†áÁ≠æÁ≠õÈÄâ
    async filterByTag(tagId) {
      try {
        const response = await apiRequest(`/community/tags/${tagId}?page=1&pageSize=${this.pageSize}`)
        
        this.posts = response.data
        this.hasMorePosts = response.data.length === this.pageSize
      } catch (error) {
        console.error('ÊåâÊ†áÁ≠æÁ≠õÈÄâÂ§±Ë¥•:', error)
      }
    },
    
    // Âä†ËΩΩÈÄöÁü•
    async loadNotifications() {
      try {
        const response = await apiRequest('/community/notifications')
        
        this.notifications = response.data
        this.unreadNotifications = this.notifications.filter(n => !n.isRead).length
      } catch (error) {
        console.error('Âä†ËΩΩÈÄöÁü•Â§±Ë¥•:', error)
      }
    },
    
    // ÊòæÁ§∫ÈÄöÁü•
    showNotification() {
      this.showNotificationModal = true
    },
    
    // Ê†áËÆ∞ÈÄöÁü•Â∑≤ËØª
    async markNotificationAsRead(notificationId) {
      try {
        await apiRequest(`/community/notification/read/${notificationId}`, 'POST')
        
        // Êõ¥Êñ∞Êú¨Âú∞Áä∂ÊÄÅ
        const notification = this.notifications.find(n => n.id === notificationId)
        if (notification) {
          notification.isRead = true
          this.unreadNotifications--
        }
      } catch (error) {
        console.error('Ê†áËÆ∞ÈÄöÁü•Â∑≤ËØªÂ§±Ë¥•:', error)
      }
    },
    
    // Ëé∑ÂèñÁî®Êà∑Âêç
    getUserName(userId, post) {
      // ‰ºòÂÖà‰ªépost.user‰∏≠Ëé∑ÂèñÁúüÂÆûÂßìÂêç
      if (post && post.user && post.user.realName) {
        return post.user.realName
      }
      
      // Á°Æ‰øùresidentsÊòØÊï∞ÁªÑ
      if (!Array.isArray(this.residents)) {
        return 'Áî®Êà∑'
      }
      
      const user = this.residents.find(r => r.id === userId)
      return user ? (user.realName || user.name || 'Áî®Êà∑') : 'Áî®Êà∑'
    },
    
    // Ê†ºÂºèÂåñÊó∂Èó¥
    formatTime(timestamp) {
      if (!timestamp) return ''
      
      const date = new Date(timestamp)
      const now = new Date()
      const diff = now - date
      
      const minutes = Math.floor(diff / (1000 * 60))
      const hours = Math.floor(diff / (1000 * 60 * 60))
      const days = Math.floor(diff / (1000 * 60 * 60 * 24))
      
      if (minutes < 60) {
        return `${minutes}ÂàÜÈíüÂâç`
      } else if (hours < 24) {
        return `${hours}Â∞èÊó∂Ââç`
      } else {
        return `${days}Â§©Ââç`
      }
    },
    
    // ËøîÂõûÈ¶ñÈ°µ
    goHome() {
      this.$router.push('/resident')
    },
    
    // Ëé∑ÂèñÈöêÁßÅÊñáÊú¨
    getPrivacyText(privacy) {
      return privacy === 'public' ? 'ÂÖ¨ÂºÄ' : 'ÊåáÂÆöÁî®Êà∑ÂèØËßÅ'
    }
  }
}
</script>

